.Navbar {
    border-right: 4px solid;
    border-left: 4px solid;
    text-align: right;
    padding: 2%;
    background: #00bcd4;
}
.ListLink {
    display: flex;
    justify-content: space-around;
}




.LinkBar {
    text-align: center;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    width: 15%;
    color: #fff;
    font-weight: 700;
    transition:   font-size 0.4s;
    height: 5vh;

}
.LinkBar ::after {
    content: "";
    display: block;
    width: 0%;
    height: 3px;
    background-color: #fff;
    margin: 7% auto 0;
    transition: width 0.4s;
}

.LinkBar :hover:after{
    width: 85%;
}
.LinkBar :hover{
    font-size: 1.1rem;
}

.Link {
    font-style: normal;
}

.dropdown{
    position: relative;
}

.dropdown-menu {
    z-index: 1;
    text-align: left;
    background: white;
    left: 0%;
    width: 200%;
    display: block;
    display: none;
    position:absolute;
    border: 1px ;
    border-radius: 2px;
    height: 100%;

}
.itemlist {
    padding-top: 2%;
    border-bottom: 2px solid #7bc7d1;
    color: black;
    display: block;
    text-align: center;
    font-style: normal;
}

.DropDownItem {
    text-decoration: none;
    font-weight: 700;
}

.itemlist {
    padding: 6% 0;
    border-bottom: 3px solid #068fa1;
    color: black;
    display: block;
    text-align: center;
    font-style: normal;
}
.itemlist:hover {
   text-decoration: underline;

}

.dropdown:hover .dropdown-menu{
    display: block;
}

.visTab {
    display: block;
    width: 4%;
    height: 2px;
    background: #fff;
}



@media screen and (min-width:769px){
}

@media screen and (max-width:768px){
    .ListLink {
        justify-content: space-between;
        align-items: center;
    }
    .ListLink{
        flex-direction:row;
    }
    .LinkBar {
        width: 15%;
    margin: 2% 0;
    }
    .visTab{
        display: block;
    }
}
@media screen and (max-width:550px){
    .LinkBar {
        width: 25%;
    margin: 2% 0;
    }
}
